import React, { useEffect, useState } from 'react';
import { Link } from "react-router-dom";
import axios from 'axios';
import AdminMenu from './AdminMenu';

export default function Admindashboard() {
    const [adminDet, setAdminDet] = useState({
        offerLst: '',
        inqLst: '',
        contLst: ''
    });
    const [srvIdErr, setSrvIdErr] = useState(false);
    const [srvIdSucc, setSrvIdSucc] = useState(false);
    const [idF, setIdF] = useState('');
    const [idS, setIdS] = useState('');
    const [idT, setIdT] = useState('');
    const [errors, setErrors] = useState({
        idF: '',
        idS: '',
        idT: ''
    });
    const [homeIds, setHomeIds] = useState([]);
    const [servErr, setSrvErr] = useState(false);
    useEffect(() => {
        let token = localStorage.getItem("admin");
        if (token) {
            axios.get('api/admindash', {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setAdminDet({
                    offerLst: res.data.offerList,
                    inqLst: res.data.inqList,
                    contLst: res.data.contList
                })
            })
            .catch((err) => {
                setSrvErr(true);
            });
            axios.get('api/gethomeids', {
                headers: {
                    "x-access-token" : token,
                    "Content-Type" : "application/json"
                }
            }).then((res) => {
                setHomeIds(res.data);
                setIdF(res.data[0]);
                setIdS(res.data[1]);
                setIdT(res.data[2]);
            })
            .catch((err) => {
                console.log(err);
                setSrvErr(true);
            });
        }
    }, []);
    const idSubmit = (e) => {
        e.preventDefault();
        let errorCount = 0;
        if(idF === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, idF: 'Id is Required'}
            });
        } else {
            setErrors((prevState)=> {
                return {...prevState, idF: ''}
            });
        } if(idS === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, idS: 'Id is Required'}
            });
        } else {
            setErrors((prevState)=> {
                return {...prevState, idS: ''}
            });
        } if(idT === '') {
            errorCount++;
            setErrors((prevState)=> {
                return {...prevState, idT: 'Id is Required'}
            });
        } else {
            setErrors((prevState)=> {
                return {...prevState, idT: ''}
            });
        }
        if(errorCount === 0) {
            let data = [idF, idS, idT]
            console.log(data, 'homeids');
            let token = localStorage.getItem("admin");
            if (token) {
                axios.post('api/homeids', {arr: data}, {
                    headers: {
                        "x-access-token" : token,
                        "Content-Type" : "application/json"
                    }
                }).then(() => {
                    setSrvIdErr(false);
                    setSrvIdSucc(true);
                    setTimeout(function() {
                        setSrvIdErr(false);
                        setSrvIdSucc(false);
                    }, 2000);
                })
                .catch(() => {
                    setSrvIdErr(true);
                    setSrvIdSucc(false);
                    setTimeout(function() {
                        setSrvIdErr(false);
                        setSrvIdSucc(false);
                    }, 2000);
                });
            }
        }
    }
    return (
        <div className="admin-pages container">
            <div className="row">
                <div className="col-md-3">
                    <AdminMenu />
                </div>
                {!servErr?<div className="col-md-9">
                    <h3 className="mb-4">Dashboard</h3>
                    <div className='row'>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.offerLst}</span>
                                Unread Offered from users
                                <div className="admin_dash_quote"><Link to="/offered" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.inqLst}</span>
                                Unread Inquiries from users
                                <div className="admin_dash_quote"><Link to="/inquiries" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                        <div className='col-md-4'>
                            <div className='admin_boxes'>
                                <span className='admin_count'>{adminDet.contLst}</span>
                                Unread Contacts from users
                                <div className="admin_dash_quote"><Link to="/contacts" className="readmore-btn">View All</Link></div>
                            </div>
                        </div>
                    </div>
                    <div>&nbsp;</div>
                    <div className='row'>
                        <div className='col-md-6'>
                            <Link to="/addproperty" className="btn btn-outline-secondary btn-block">Add Property</Link>
                        </div>
                        <div className='col-md-6'>
                            <Link to="/editproperty" className="btn btn-outline-secondary btn-block">Edit Property</Link>
                        </div>
                    </div>
                    <form className="sub_id_forms" autoComplete="off" onSubmit={idSubmit}>
                        <h3 className="mb-4">Ids to show in home page</h3>
                        <div className='form-row'>
                            <div className='form-group col-md-6'>
                                <label htmlFor="first_id">First ID</label>
                                <input type="text" className="form-control" id="first_id" defaultValue={homeIds[0]} onChange={(e)=>setIdF(e.target.value)} placeholder="Paste id of first property" />
                                <span className="form_err">{errors.idF}</span>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor="second_id">Second ID</label>
                                <input type="text" className="form-control" id="second_id" defaultValue={homeIds[1]} onChange={(e)=>setIdS(e.target.value)} placeholder="Paste id of second property" />
                                <span className="form_err">{errors.idS}</span>
                            </div>
                            <div className='form-group col-md-6'>
                                <label htmlFor="third_id">Third ID</label>
                                <input type="text" className="form-control" id="third_id" defaultValue={homeIds[2]} onChange={(e)=>setIdT(e.target.value)} placeholder="Paste id of third property" />
                                <span className="form_err">{errors.idT}</span>
                            </div>
                            <div className='form-group col-md-6'>
                                <label style={{display: "block"}}>&nbsp;</label>
                                <button type="submit" className="btn btn-primary">Add List Too Home</button>
                            </div>
                        </div>
                        {srvIdSucc?<p className='success_msge'>Updated Successfully.</p>:""}
                        {srvIdErr?<div className="col-sm-12 server_error">
                            Not able to update please try again later
                        </div>:""}
                    </form>
                </div>:<div className="col-9">
                    <div className="error-page text-center">
                        <div style={{height:"60px"}}>&nbsp;</div>
                        <h5 className="not_fnd_hd">OOPS... Something went wrong!</h5>
                        <p className="not_fnd_body">
                            Please try again later or<br />
                            <Link to="/" className="readmore-btn">Back to Home Page</Link>
                        </p>
                    </div>
                </div>}
            </div>
        </div>
    )
}
